






import Swal from 'sweetalert2';
import { Component, Vue } from 'vue-property-decorator';
import { Action, State } from 'vuex-class';
import { IInputInfo, IMergeUserGuid } from '@/types/interface';

@Component
export default class AssociationTemplate extends Vue {
  @State('userId') stateUserId!: string;
  @State('name') stateName!: string;
  @Action('addCkMember') addCkMember!: (params: IInputInfo) => Promise<string>;
  @Action('getMemberUser') getMemberUser!: (params: IInputInfo) => Promise<string>
  @Action('mergeUserApply') mergeUserApply!: (params: IMergeUserGuid[]) => Promise<void>;

  isAssociated = false;

  get isMemberProcess() {
    return this.$route.query.process === 'member';
  }

  created() {
    if (this.isMemberProcess) {
      this.isAssociated = true;
      window.addEventListener('message', this.receiveMessage, false);
    } else {
      Swal.fire({
        width: 300,
        showCloseButton: false,
        showCancelButton: true,
        background: '#FFFFFF',
        title: '是否關聯公職王帳號',
        text: '',
        customClass: {
          title: 'modal-title',
          content: 'modal-content',
          confirmButton: 'modal-confirm-button-half',
          cancelButton: 'modal-cancel-button-half',
        },
        confirmButtonText: '關聯',
        cancelButtonText: '略過',
        reverseButtons: true,
      }).then((res) => {
        if (res.isConfirmed) {
          this.isAssociated = true;
          window.addEventListener('message', this.receiveMessage, false);
        } else this.$router.push('/login/set_member_profile');
      });
    }
  }

  receiveMessage(e: any) {
    if (e.origin === 'https://www.public.com.tw' && e.data.Data) {
      console.log(JSON.stringify(e));
      const params = {
        // eslint-disable-next-line
        Approve_Key: e.data.Data.certification_guid,
        PID: e.data.Data.id,
        Name: this.stateName,
        Source: 'CF04',
        Status: '0',
      };
      if (e.data.Data.Code === '0000') {
        this.addCkMember(params)
          .then((ckId: string) => {
            const temp = [{
              UserGuid: this.stateUserId,
              MergeGuid: ckId,
            }];
            return this.mergeUserApply(temp);
          })
          .then(() => {
            this.showMessage(true, '');
          })
          .catch((errMsg: string) => {
            this.showMessage(false, errMsg);
          });
      } else if (e.data.Data.Code === '7997' || e.data.Data.Code === '7998' || e.data.Data.Code === '7999') {
        this.showMessage(false, e.data.Data.Message);
      }
    }
  }

  showMessage(flag: boolean, msg: string) {
    Swal.fire({
      width: 300,
      showCloseButton: false,
      background: '#FFFFFF',
      title: flag ? '關聯成功' : '關聯失敗',
      text: msg,
      customClass: {
        title: 'modal-title',
        content: 'modal-content',
        confirmButton: 'modal-confirm-button',
      },
      confirmButtonText: '確認',
    }).then(() => {
      if (this.isMemberProcess) this.$router.push('/member/edit_profile');
      else this.$router.push('/login/set_member_profile');
    });
  }
}
